.superclick-tabs {
    border-top: 1px solid #aec1ed;
    padding-top: 20px;
}
.sponsor {
    & p {
        font-size: 10px;
    }

    .btn {
        border-radius: 8px;
        padding: 22px 12px;
        font-weight: 700;
        font-size: 16px;
        text-transform: uppercase;
        text-align: center;
        width: 100%;
        margin-bottom: 12px;
    }

    .btn-bordered {
        width: 100%;
        border-radius: 8px;
        margin-bottom: 12px;
        & p {
            border-radius: 6px;
            font-weight: 700;
            font-size: 16px;
            text-align: center;
            color: #3f2aa9;
            padding: 22px 12px;
        }
    }
}
.sponsor-tabs {
    .tabs__links {
        grid-template-columns: repeat(3, 1fr);

        button p {
            font-size: 10px;
        }

        &.pink {
            button {
                p {
                    padding: 4px 12px;
                }
            }
        }
    }
}
.sponsor-block {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-bottom: 20px;

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0 0 100px;
        height: 48px;
        padding: 4px;
        border-radius: 8px;
        background: #fff;
        & img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    p {
        font-weight: 700;
        font-size: 12px;
        color: #1a2b63;
    }
}
.sponsor-bg {
    width: 100%;
    height: 150px;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 20px;

    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.sponsor-sale {
    font-weight: 600;
    font-size: 20px;
    text-align: center;
    color: #3f2aa9;
}
.sponsor-statement {
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;

    & p {
        font-weight: 700;
        font-size: 20px;
        color: #1a2b63;
    }
}
.sponsor-task {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-weight: 500;
    font-size: 10px;
    position: relative;

    &::before {
        content: "";
        display: block;
        border-radius: 8px;
        width: 8px;
        height: 8px;
    }
    &.red {
        color: #fe3745;
        &::before {
            background: #fe3745;
        }
    }
}
