.nav-tabs {
    position: fixed;
    bottom: 20px;
    left: 20px;
    right: 20px;
    height: 60px;
    z-index: 10;
    &__row {
        display: flex;
        gap: 11px;
        align-items: center;
        max-width: fit-content;
        border-radius: 8px;
        padding: 8px;
        background: #fff;
        margin: 0 auto;
    }
    &__link {
        border-radius: 4px;
        padding: 4px;
        width: 44px;
        height: 44px;
        background: #e9efff;
        font-weight: 700;
        text-align: center;
        color: #3f2aa9;

        & svg {
            height: 20px;
            width: 17px;
            object-fit: contain;
            margin-bottom: 6px;
            path {
                fill: #3f2aa9;
            }
        }

        p {
            font-size: 6px;
        }

        &.active {
            background: #3f2aa9;

            svg path {
                fill: #fff;
            }
            & p {
                color: #fff;
            }
        }
    }
}
