.rating-places {
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #3f2aa9;
    border-radius: 4px;
    padding: 8px 12px;
    margin-bottom: 12px;

    & p:first-child {
        margin-bottom: 4px;
        font-weight: 500;
        font-size: 8px;
        background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    & p:last-child {
        margin-bottom: 0px;
        font-weight: 700;
        font-size: 8px;
        background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}
.rating-players {
    display: flex;
    flex-direction: column;
    gap: 2px;
    margin-bottom: 20px;

    &__player {
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 4px 12px;
        background: #fff;
        &:first-child {
            border-radius: 12px 12px 0px 0px;
        }
    }
    &__place {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        background: #3f2aa9;
        font-weight: 700;
        font-size: 10px;
        text-align: center;
        color: #fff;
        padding: 4px 6px;
    }
    &__count {
        margin-left: auto;
        & p:first-child {
            font-weight: 700;
            font-size: 10px;
            color: #1a2b63;
            margin-bottom: 4px;
        }
        & p:last-child {
            font-weight: 400;
            font-size: 8px;
            text-align: right;
            color: #1a2b63;
            margin-bottom: 0px;
        }
    }

    h3 {
        font-weight: 700;
        font-size: 10px;
        color: #1a2b63;
    }
}
