.task {
    &:not(:last-child) {
        margin-bottom: 20px;
    }
    &__header {
        h2 {
            font-weight: 700;
            font-size: 14px;
            color: #1a2b63;
            margin-bottom: 8px;
        }
        p {
            font-weight: 400;
            font-size: 12px;
            color: #1a2b63;
            margin-bottom: 8px;
        }
    }
    &__task {
        display: flex;
        gap: 10px;
        border-radius: 8px;
        padding: 8px 12px;

        position: relative;
        &.blue {
            background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
        }
        &.purple {
            background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
        }
        &.green {
            background: linear-gradient(360deg, #00900e 0%, #04cc00 100%);
        }
        h3 {
            font-weight: 700;
            font-size: 14px;
            color: #fff;
            margin-bottom: 2px;
        }
        p {
            font-weight: 500;
            color: #fff;
            font-size: 10px;
        }
        &:not(:last-child) {
            margin-bottom: 8px;
        }
    }
    &__ad {
        position: absolute;
        border-radius: 12px;
        padding: 0px 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 14px;
        text-align: center;
        color: #1a2b63;
        top: -5px;
        right: 28px;
        background: #fff;
    }
    &__new {
        position: absolute;
        border-radius: 8px;
        width: 8px;
        height: 8px;
        right: 6px;
        top: 6px;
        background: #fe3745;
    }
}
