.partner {
    margin-top: 20px;
    & p,
    h3,
    h4 {
        color: #fff;
    }
    h3 {
        font-weight: 700;
        font-size: 10px;
    }
    h4 {
        font-weight: 500;
        font-size: 6px;
    }
    &__header {
        margin-bottom: 8px;
        h2 {
            font-weight: 700;
            font-size: 14px;
            color: #1a2b63;
        }
    }
    &__partner {
        &:not(:last-child) {
            margin-bottom: 8px;
        }
        border-radius: 8px;
        padding: 8px;
        background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
    }
    &__partner-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 8px;
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        padding: 4px 8px;
        background: #fff;
        width: 90px;
        height: 32px;
        margin-bottom: 8px;
        & img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }
    }
    &__text {
        p {
            font-weight: 400;
            font-size: 6px;
        }
    }
    &__clicks {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;

        & p {
            font-weight: 700;
            font-size: 10px;
        }
    }
    &__coins {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 2px;
    }
    &__footer {
        font-weight: 400;
        font-size: 6px;
        text-align: center;
        color: #1a2b63;
        margin-top: 8px;
    }
}
