.rocket-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
    margin: 8px 0px;
    overflow: hidden;
    position: relative;
}
.rocket {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 35px 0px;
    position: relative;
    z-index: 2;
    height: 100%;
    animation: rocketDiagonalSway 3s ease-in-out infinite;
}
.rocket-body {
}

.rocket-fire {
    position: relative;
    top: -20px;
    animation: flameFlicker 1s ease-in-out infinite;
}
@keyframes flameFlicker {
    0% {
        scale: 1;
        opacity: 1;
    }
    50% {
        scale: 1.05; /* Увеличение масштаба */
        opacity: 0.9; /* Уменьшение прозрачности */
    }
    100% {
        scale: 1;
        opacity: 1;
    }
}
@keyframes rocketDiagonalSway {
    0% {
        transform: translateX(0%) translateY(0);
    }
    25% {
        transform: translateX(0%) translateX(-5px) translateY(-3px);
    } /* Влево-вверх */
    50% {
        transform: translateX(0%) translateX(5px) translateY(-3px);
    } /* Вправо-вверх */
    75% {
        transform: translateX(0%) translateX(5px) translateY(-1px);
    }
    100% {
        transform: translateX(0%) translateY(0);
    }
}

.stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.star {
    position: absolute;
    background-color: white;
    border-radius: 50%;
}
